@font-face {
    font-family: matrix-code;
    src: url('./fonts/matrix_code_nfi.ttf');
}

@font-face{
    font-family: visitor;
    src: url('./fonts/visitor2.ttf');
}

//Rubik font
@import url('https://fonts.googleapis.com/css2?family=Rubik&display=swap');


body{
    font-family: 'Rubik', sans-serif;
    text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.25);

    *{
        box-sizing:border-box;
    }

    #wrapper{ //contains all content.
        margin:auto;
        width:968px;
        min-width:968px;
        padding-top:15px;
        padding-bottom:15px;   

        display:flex;
        gap:15px;
        flex-direction:column;
    }
}



//SCROLLBAR

/* width */
::-webkit-scrollbar {
     width:10px;
}
  
/* Track */
::-webkit-scrollbar-track {
    background:#1f1f1f;
}
  
/* Handle */
::-webkit-scrollbar-thumb {
    background:#5a5a5a;
    border-radius:10px;
}
  
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background:#747474;
}